import React, { useContext } from "react";
import { Link } from "react-router-dom";

import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";

import { Box, Typography, Button, IconButton } from "@material-ui/core";
import MenuIcon from "@material-ui/icons/Menu";

import Context from "../../../store/context";

const useStyles = makeStyles((theme) =>
  createStyles({
    root: {
      flexGrow: 1,
    },
    menuButton: {
      marginLeft: theme.spacing(1),
      fontSize: 17,
      fontWeight: 400,
    },
    title: {
      flexGrow: 1,
    },
  })
);

const Header = () => {
  const { globalState, globalDispatch } = useContext(Context);
  const handleLogout = () => {
    localStorage.removeItem("user");
    localStorage.removeItem("token");
    globalDispatch({
      type: "LOGOUT",
    });
  };
  const classes = useStyles();
  return (
    <Box display="flex" bgcolor="info.main" p={2} alignItems="center">
      <Typography>CHRIS 180</Typography>
      {!globalState.isLoggedIn && <Link href="#"></Link>}
      {globalState.isLoggedIn && (
        <Button>
          <Link to="/meeting">Add Supervision</Link>
        </Button>
      )}

      {!globalState.isLoggedIn && <Link href="#"></Link>}
      {globalState.isLoggedIn && (
        <Button>
          <Link to="/supervision">My Supervisions</Link>
        </Button>
      )}
      <Box flexGrow={2} textAlign="right">
        <IconButton>
          <MenuIcon />
        </IconButton>
      </Box>
      {!globalState.isLoggedIn && <Link href="#"></Link>}
      {globalState.isLoggedIn && (
        <Link onClick={handleLogout} to="/">
          Log Out
        </Link>
      )}
    </Box>
  );
};

export default Header;
